
import { Component, Vue } from "vue-property-decorator";
import { Match } from "@/models/Match";
import axios from "axios";
import moment from "moment";
import store from "@/store/index";

@Component
export default class Predictor extends Vue {
  private axios = axios.create({});
  private matches: Array<Match> = [];
  private date = moment();
  private username = store.state.username;
  private predictionSubmitted = false;

  mounted() {
    this.loadData();
  }

  private loadData() {
    this.matches = [];
    this.axios
      .get(
        `https://wcpredictor.fun/api/matches?date=${this.date.format("YYYYMMDD")}`
        // `https://fotmob.com/api/matches?date=${this.date.format("YYYYMMDD")}`
      )
      .then((response) => {
        console.log(response);
        let worldCupData = response.data.leagues.filter((leagues: any) => leagues.parentLeagueName === "World Cup" || leagues.parentLeagueId === 77);
        worldCupData.forEach((l: any) => {
          l.matches.forEach((match: any) => {
            this.matches.push(new Match(match.home.name, match.away.name, match.status.started, match.home.score, match.away.score));
          });
        });
      })
      .catch((error: any) => {
        alert(JSON.stringify(error));
      });
  }

  private savePrediction() {
    let bodyJson: any = [];
    this.matches.forEach((match) => {
      if (match.predHomeTeamScore && match.predAwayTeamScore) {
        bodyJson.push({
          match: `${match.homeTeam}${match.awayTeam}`,
          user: this.$store.state.username,
          predictionHomeScore: Number(match.predHomeTeamScore),
          predictionAwayScore: Number(match.predAwayTeamScore),
        });
      }
    });

    this.axios
      .post(`https://wcpredictor.fun/api/predict`, bodyJson)
      .then((response) => {
        this.predictionSubmitted = true;
        this.matches = [];
      })
      .catch(function (error) {
        console.log(error);
      });
  }

  private updateMatches() {
    let bodyJson: any = [];
    this.matches.forEach((match) => {
      if (match.started) {
      bodyJson.push({
        match: `${match.homeTeam}${match.awayTeam}`,
        homeTeam: match.homeTeam,
        awayTeam: match.awayTeam,
        homeScore: match.homeTeamScore,
        awayScore: match.awayTeamScore,
      })
      }

  });

    this.axios
      .post(`https://wcpredictor.fun/api/results`, bodyJson)
      .then((response) => {
        console.log(response);
        this.matches = [];
      })
      .catch(function (error) {
        console.log(error);
      });
    console.log(JSON.stringify(bodyJson));
  }
}
